import { ReminderResetDaily, ReminderTriggerValue } from '../../../api';

export type ReminderMode = 'once' | 'repeating';

export enum ReminderType {
  ODOMETER = 'odo',
  ENGINE_HOURS = 'hours',
  TIME_BASED = 'time',
}

export interface Reminder {
  name: string;
  mode: ReminderMode;
  timeZoneId?: string;
  trigger?: ReminderTriggerValue;
  reset?: {
    type?: 'none' | 'daily' | 'weekly' | 'monthly';
    counter?: string;
    daily?: ReminderResetDaily;
    weekly?: {
      days: string[];
    };
    monthly?: {
      months: string[];
      day: string;
    };
  };
}
