import { Injectable } from '@angular/core';
import { isDefined, isNullOrUndefined } from '../../shared/utils';
import { WEEKDAY_OPTIONS } from '../../features/administration/models';

interface FilterValue {
  operator?: '=' | '!=' | 'in' | '>' | '<';
  value: string | number | unknown[];
  wrap?: boolean;
}

@Injectable({
  providedIn: 'root',
})
export class UtilityService {
  convertBlobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        resolve(reader.result as string);
      };
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  }

  /**
   * Constructs an RQL filter string from the given filters object.
   * @param filters An object containing filter criteria.
   * @returns A string representing the RQL filter.
   */
  private handleArrayValue(key: string, value: unknown[]): string {
    return `${key}=in=(${value.map((v) => encodeURIComponent(String(v))).join(',')})`;
  }

  private handleFilterValueArray(
    key: string,
    values: unknown[],
    operator: string,
  ): string {
    if (values.length === 0) return '';
    const conditions = values
      .map(
        (v) =>
          `${key}${operator === 'in' ? '=' : operator}${encodeURIComponent(String(v))}`,
      )
      .join('|');
    return values.length > 1 ? `(${conditions})` : conditions;
  }

  private handleFilterValue(key: string, value: FilterValue): string {
    const operator = isDefined(value.operator) ? value.operator : '=';
    let condition: string;
    if (Array.isArray(value.value)) {
      condition = this.handleFilterValueArray(key, value.value, operator);
    } else {
      condition = `${key}${operator}${String(value.value)}`;
    }
    return (value.wrap ?? false) ? `(${condition})` : condition;
  }

  constructRqlFilter(filters: Record<string, unknown>): string {
    return Object.entries(filters)
      .map(([key, value]) => {
        if (Array.isArray(value)) {
          return this.handleArrayValue(key, value);
        } else if (typeof value === 'string') {
          return `${key}=eq=${encodeURIComponent(value)}`;
        } else if (typeof value === 'number') {
          return `${key}=eq=${value}`;
        } else if (typeof value === 'object' && value !== null) {
          return this.handleFilterValue(key, value as FilterValue);
        }
        return '';
      })
      .filter(Boolean)
      .join('&');
  }

  /**
   * Converts a string to title case (capitalizes first letter of each word)
   * @param str The string to convert
   * @returns The title cased string
   */
  toTitleCase(str: string): string {
    if (!str) return '';
    return str
      .toLowerCase()
      .split(' ')
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  }

  escapeRqlValue(value: string) {
    const result = encodeURIComponent(
      value.replace(/[%/\\^$+?.()|[\]{}]/g, '\\$&'),
    )
      .replace(/!/g, '%21')
      .replace(/~/g, '%7E')
      .replace(/</g, '%3C')
      .replace(/>/g, '%3E')
      .replace(/\(/g, '%28')
      .replace(/\)/g, '%29')
      .replace(/'/g, '%27')
      .replace(/\*/g, '%2A');
    return result; /*?*/
  }

  decamelize(str?: string): string {
    if (isNullOrUndefined(str)) return '';
    return str
      .replace(/([a-z])([A-Z])/g, '$1 $2')
      .replace(/_/g, ' ')
      .toLowerCase()
      .trim();
  }

  getWeekdaysString(weekdays: string[]): string {
    return WEEKDAY_OPTIONS.filter((option) => weekdays.includes(option.value))
      .map((option) => option.label)
      .join(', ');
  }
}
