import { TreeNode } from 'primeng/api';
import { DEFAULT_SYSTEM_ID } from '../../../core/models';
import { ClientListItem, ClientResponse } from '../../../api';

export const ADMIN_TREE = {
  PAGINATION: {
    DEFAULT_PAGE_SIZE: 100,
    INITIAL_PAGE: 0,
  },

  ICONS: {
    SUPER_ADMIN: 'pi pi-crown',
    DISTRIBUTOR: 'pi pi-building',
    VENDOR: 'pi pi-building-columns',
    GROUP: 'pi pi-folder',
    CLIENT: 'pi pi-user',
  },

  NODE_TYPES: {
    SUPER_ADMIN: 'super_admin',
    DISTRIBUTOR: 'distributor',
    VENDOR: 'vendor',
    GROUP: 'group',
    CLIENT: 'client',
    LOAD_MORE: 'load_more',
  } as const,

  LABELS: {
    SUPER_ADMIN: '(Super Admin)',
    DISTRIBUTOR: '(Distributor)',
    VENDOR: '(Vendor)',
    GROUP: '(Company Group)',
    CLIENT: '(Client)',
  },

  DEFAULT_GROUP_ID: DEFAULT_SYSTEM_ID,
  DELETED_STATE: 'deleted',
} as const;

export interface FilterCriteria {
  value: string;
  operator: string;
}

interface FilterCondition {
  value: string;
  operator: string;
}

export interface ClientFilters {
  group: FilterCondition;
  state: FilterCondition;
  [key: string]: FilterCondition | string;
}

export interface AdminTreeNodeData {
  id?: string;
  name?: string | null;
  type: (typeof ADMIN_TREE.NODE_TYPES)[keyof typeof ADMIN_TREE.NODE_TYPES];
}

export interface AdminTreeNode extends TreeNode {
  key: string;
  data: AdminTreeNodeData;
  children?: AdminTreeNode[];
  expanded?: boolean;
  leaf?: boolean;
  selected?: boolean;
  selectable?: boolean;
  icon?: string;
  loading?: boolean;
  styleClass?: string;
}

export interface ClientTreeNode extends AdminTreeNode {
  data:
    | (ClientResponse & { type: typeof ADMIN_TREE.NODE_TYPES.CLIENT })
    | (ClientListItem & { type: typeof ADMIN_TREE.NODE_TYPES.CLIENT });
}
